import React, {useState} from 'react'
import { useStaticQuery, graphql, Link } from 'gatsby'
import BreadcrumContent from '../../../../../Global/Pages/BreadcrumbContent.component'
import Pagination from '../../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import SidebarFeatured from '../../../../../Global/Pages/SideBarFeaturedListing.component'
import GridFeaturedProperties from '../../../../../Global/Pages/Listing/Grid/GridFeaturedProperties.component'
import CategoriesProperty from '../../../../../Global/Pages/CategoriesProperty.component'
import PropertyGridItem from '../../../../../Global/Pages/Listing/PropertyGridItem.component'
import ListGridView from '../../../../../Global/Pages/Listing/ListGridView.component'

import { SearchResult } from '../../../../../Global/Pages/Listing/Grid/GridSearchResult.component'
const V1Section=()=>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const [toggleList,setToggleList] = useState(false)
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    console.log(toggleList)

    const data = useStaticQuery(graphql`
        query{
            fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp5: file(relativePath: {eq: "property/fp5.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp15: file(relativePath: {eq: "property/fp15.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp16: file(relativePath: {eq: "property/fp16.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp17: file(relativePath: {eq: "property/fp17.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp18: file(relativePath: {eq: "property/fp18.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp19: file(relativePath: {eq: "property/fp19.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp20: file(relativePath: {eq: "property/fp20.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
       
        
    return(
        <section className="our-listing bgc-f7 pb30-991">
			<div className="container">
                <div className="row">
				<div className="col-lg-12">
					<div className="listing_sidebar dn db-991">
						<div className="sidebar_content_details style3">
							<div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
								<div className="sidebar_advanced_search_widget">
									<h4 className="mb25">
                                    Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                    </Link>
                                    </h4>
									<AdvanceSearchWidgetSidebar style2 index="1"/>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
                <div className="row">
                    <div className="col-lg-6">
                    <BreadcrumContent title="Simple Listing – Grid View" active="Simple Listing – Grid View"/>
                    </div>
                
                 <div className="col-lg-6">
                    <div className="listing_list_style mb20-xsd tal-991">
                        <ul className="mb0">
                            <li className="list-inline-item" onClick={()=>setToggleList(false)}><Link to="#"><span className="fa fa-th-large"></span></Link></li>
                            <li className="list-inline-item" onClick={()=> setToggleList(true)}><Link to="#"><span className="fa fa-th-list"></span></Link></li>
                        </ul>
                    </div>
                    <div className="dn db-991 mt30 mb0">
						<div id="main2">
							<span id="open2" className="flaticon-filter-results-button filter_open_btn style2" onClick={handleSidebar}> Show Filter</span>
						</div>
					</div>
                </div>
                </div>
                <div className="row">
                <div className="col-lg-4 col-xl-4">
                    <div className="sidebar_listing_grid1 dn-991">
						<div className="sidebar_listing_list">
							<div className="sidebar_advanced_search_widget">
                                <AdvanceSearchWidgetSidebar index="2"/>
                            </div>
                        </div>
                        <GridFeaturedProperties/>
                        <CategoriesProperty/>
                        <SidebarFeatured HeaderTitle="Recently Viewed"/>
                    </div>
                    </div>
                    { !toggleList ? 
                    <div className="col-md-12 col-lg-8">
                        <div className="row">
                         <SearchResult/>
                        </div>
                        
                        <div className="row">
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                            <PropertyGridItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Sale'
                                    price="13000"category="Single Family Home"
                                    title="Luxury Family Home"
                                    >
                                        <div className="item">
                                            <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                        </div>
                                        <div className="item">
                                            <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg}alt="fp4.jpg"/>
                                        </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                            <PropertyGridItem listfor='For Rent'
                                    price="13000"category="Apartment"
                                    title="Luxury Family Home"
                                    featured
                                    >
                                        <div className="item">
                                            <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg} alt="fp4.jpg"/>
                                        </div>
                                        <div className="item">
                                            <img className="img-whp" src={data.fp5.childImageSharp.fluid.originalImg}alt="fp5.jpg"/>
                                        </div>
                            </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Sale'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp15.childImageSharp.fluid.originalImg} alt="fp15.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp16.childImageSharp.fluid.originalImg}alt="fp16.jpg"/>
                                    </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp16.childImageSharp.fluid.originalImg} alt="fp16.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp17.childImageSharp.fluid.originalImg}alt="fp17.jpg"/>
                                    </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Sale'
                                    price="13000"category="Single Family Home"
                                    title="Luxury Family Home"
                                    >
                                        <div className="item">
                                            <img className="img-whp" src={data.fp17.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                        </div>
                                        <div className="item">
                                            <img className="img-whp" src={data.fp18.childImageSharp.fluid.originalImg}alt="fp4.jpg"/>
                                        </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Rent'
                                    price="13000"category="Apartment"
                                    title="Luxury Family Home"
                                    featured
                                    >
                                        <div className="item">
                                            <img className="img-whp" src={data.fp18.childImageSharp.fluid.originalImg} alt="fp4.jpg"/>
                                        </div>
                                        <div className="item">
                                            <img className="img-whp" src={data.fp19.childImageSharp.fluid.originalImg}alt="fp5.jpg"/>
                                        </div>
                            </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp19.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp20.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyGridItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyGridItem>
                            </div>
                            <div className="col-lg-12 mt20">
                                <Pagination/>
                            </div>                          
                        </div>
                    </div>
                        :
                    <div className="col-md-12 col-lg-8">
                        <div className="row">
                         <SearchResult/>
                        </div>
                        <ListGridView/>
                    </div>
                    }
                
                </div>
            </div>
	    </section>
    )
}
export default V1Section